/**
 * #.# Editor Styles
 *
 * CSS for just Backend enqueued after style.scss
 * which makes it higher in priority.
 */
.spb_upgrade {
	display: flex;
	padding: 10px;
	background: #eee;
	justify-content: space-between;
	align-items: center;
}
.spb_upgrade p {
	margin: 0;
	padding-right: 10px;
}
.spb_upgrade a {
	background: #03bb87;
	color: #fff;
	font-weight: bold;
	text-decoration: none;
	padding: 7px 12px;
	min-width: 120px;
	text-align: center;
}

.sptcrb__abouttheauthor__backend__wrapper {
	background: #f3f3f4 !important;
	text-align: center !important;
	padding: 15px !important;
	font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans,
		Ubuntu, Cantarell, Helvetica Neue, sans-serif !important;
}

.sptcrb__abouttheauthor__backend__wrapper h3 {
	font-size: 13px !important;
	text-align: center !important;
	margin: 0 0 20px 0 !important;
	font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans,
		Ubuntu, Cantarell, Helvetica Neue, sans-serif !important;
}
.sptcrb__abouttheauthor__backend__wrapper h4 {
	font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans,
		Ubuntu, Cantarell, Helvetica Neue, sans-serif !important;
	margin: 0 !important;
	font-size: 12px !important;
}
.sptcrb__abouttheauthor__backend__wrapper-view-one-button {
	text-align: center !important;
}

.sptcrb__abouttheauthor__backend__wrapper-view-one-button button {
	background: #00bc87 !important;
	border: 0px solid #fff !important;
	color: #fff !important;
	padding: 10px 20px !important;
	cursor: pointer !important;
	font-size: 14px !important;
	margin: 10px auto !important;
	border-radius: 3px !important;
	min-width: 280px !important;
}

.sptcrb__abouttheauthor__backend__wrapper-title {
	margin-bottom: 25px !important;
}
.sptcrb__abouttheauthor__backend__image__toggle {
	background: #00bc87 !important;
	border: 0px solid #fff !important;
	color: #fff !important;
	padding: 10px 20px !important;
	cursor: pointer !important;
	font-size: 14px !important;
	margin: 10px auto !important;
	border-radius: 3px !important;
}
.sptcrb__abouttheauthor__backend__wrapper-text textarea {
	width: 100% !important;
	max-width: 310px !important;
	min-height: 100px !important;
	margin-bottom: 15px !important;
	font-size: 12px !important;
}
.sptcrb__abouttheauthor__backend__wrapper-social-media {
	display: flex !important;
	align-items: center !important;
	text-align: center !important;
	align-content: center !important;
	justify-content: center !important;
	margin-bottom: 10px !important;
}
.sptcrb__abouttheauthor__backend__wrapper-save button {
	background: #00bc87 !important;
	min-width: 150px !important;
	border: 0px solid #fff !important;
	color: #fff !important;
	padding: 10px 20px !important;
	cursor: pointer !important;
	font-size: 16px !important;
	margin: 10px auto !important;
	border-radius: 3px !important;
}

.sptcrb__abouttheauthor__backend__wrapper-social-media-column h4 {
	margin-bottom: 5px !important;
}

.sptcrb__abouttheauthor__backend__d__none {
	display: none !important;
}

.sptcrb__abouttheauthor__backend__block__center {
	display: block !important;
	text-align: center !important;
}

.sptcrb_ata_author_select {
	display: block;
	width: 100%;
}
